* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    background-color: rgb(32, 32, 32);
    color: rgb(49, 49, 49)
}

.list {
    width: 90vw;
    max-width: 1170px;
    margin: 2rem auto;
    display: grid;
    gap: 2rem;

}

.listDetail {
    background-color: rgb(199, 199, 199);
    border-radius: 10px;
    padding: 1rem 0;
    text-align: center;
    position: relative;
}

.listDetail img {
    width: 50%;
    height: 80%;
    object-fit: cover;
    padding-bottom: 1rem;
}

.listDetail h2 {
    margin-bottom: 0.5rem;

}

.listDetail button {
    padding: 1rem 0.8rem;
    border-radius: 100%;
    background-color: rgb(219, 219, 219);
    border: none;
    cursor: pointer;
}

.order {
    position: absolute;
    color: white;
    top: 0;
    left: 0;
    font-size: 1rem;
    padding: 0.8rem;
    background-color: rgb(131, 131, 131);
    border-top-left-radius: 5px;
    border-bottom-right-radius: 10px;
}

@media screen and (min-width: 768px) {
    .list {
        grid-template-columns: repeat(3, 1fr);
    }
}